import {DateProvider} from './date-provider';
import { ApplicationUtil } from './app-util';
/**
 * @param {?} value
 * @return {?}
 */
function isNotBlank(value:any) {
    return (value !== undefined && value !== null && value !== "");
}
/**
 * @param {?} value
 * @return {?}
 */
function trim(value:any){
    if (isNotBlank(value))
        if (typeof value === "string")
            return value.trim();
    return value;
};
/**
 * @param {?} value
 * @return {?}
 */
function ltrim(value:any){
    if (isNotBlank(value))
        if (typeof value === "string")
            return value.replace(/^\s+/g, '');
    return value;
}
/**
 * @param {?} value
 * @return {?}
 */
function rtrim(value:any){
    if (isNotBlank(value))
        if (typeof value === "string")
            return value.replace(/\s+$/g, '');
    return value;
}
/**
 * @param {?} value
 * @param {?} chars
 * @return {?}
 */
function blacklist(value: any, chars) {
    if (isNotBlank(value))
        if (typeof value === "string")
            return value.replace(new RegExp('[$' + chars + ']+','g'), '');
    return value;
};
/**
 * @param {?} value
 * @param {?} keepNewLines
 * @return {?}
 */
function stripLow(value:any,keepNewLines:boolean){
    let /** @type {?} */ chars:String = keepNewLines === true ? '\x00-\x09\x0B\x0C\x0E-\x1F\x7F' : '\x00-\x1F\x7F';
    return blacklist(value,chars);
}
/**
 * @param {?} value
 * @param {?} strict
 * @return {?}
 */
function toBoolean(value:any,strict:boolean){
    if (isNotBlank(value)){
        if (strict) {
            return value === '1' || value === 'true';
          }
          return value !== '0' && value !== 'false' && value !== '';
    }
    return value;
}
/**
 * @param {?} value
 * @return {?}
 */
function toFloat(value:any){
    if (isNotBlank(value) )
        if(ApplicationUtil.isNumeric(value))
            return parseFloat(value)
    return null;
}
/**
 * @param {?} value
 * @return {?}
 */
function toDouble(value:any){
        return toFloat(value)
}
/**
 * @param {?} value
 * @param {?} radix
 * @return {?}
 */
function toInt(value:any,radix:number){
    if (isNotBlank(value))
        if( ApplicationUtil.isNumeric(value))
            return parseInt(value, radix || 10);
    return null;
}
/**
 * @param {?} value
 * @param {?} radix
 * @return {?}
 */
function toString(value:any,radix:number){
    if (isNotBlank(value))
        return String(value);
    return value;
}
/**
 * @param {?} value
 * @param {?} chars
 * @return {?}
 */
function whitelist(value:any,chars:string){
    if (isNotBlank(value))
    if (typeof value === "string")
        return value.replace(new RegExp(`[^${chars}]+`, 'g'), '');
    return value;
}
/**
 * @param {?} value
 * @return {?}
 */
function toDate(value:any){
    var /** @type {?} */ dateProvider = new DateProvider();
    if(isNotBlank(value))
        if(typeof value === "string" && dateProvider.isValid(value)){
            value = dateProvider.getDate(value);
            return value;
        }
    return null;
}

export const /** @type {?} */ SANITIZERS: { [key: string]: Function } = {

    trim:trim,

    ltrim:ltrim,

    rtrim:rtrim,

    blacklist:blacklist,

    stripLow:stripLow,

    toBoolean:toBoolean,

    toDouble:toDouble,

    toFloat:toFloat,

    toInt:toInt,

    'toString':toString,

    whitelist:whitelist,

    toDate:toDate

}